@import url('https://fonts.googleapis.com/css?family=Poppins:700|Poppins:600|Poppins:500|Poppins:400|Poppins:300');
@import url('https://fonts.googleapis.com/css?family=Inter:800|Inter:700|Inter:600|Inter:500|Inter:400');
@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined');

* {
  box-sizing: border-box;
  color: #070817;
  font-family: Poppins;
  background-color: transparent;
  margin: 0px;
}


.App {
  margin-left: auto;
  margin-right: auto;
  overflow-x: hidden;
}

.content {
  margin-left: auto; /* Usar margen automático para centrar */
  margin-right: auto; /* Usar margen automático para centrar */
  text-align: center; /* Centrar texto horizontalmente si es necesario */
  padding: 0px;
}

/* NAVBAR ------------------------------------------------------------------------------------------------- */


.navbar-content {
  background-color: #ffff;
}

.navbar {
  top: 0;
  z-index: 1000;
  background-color: #FFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 30px;
  padding-right: 30px;
  box-shadow: 0px 2px 100px rgba(0, 0, 0, 0.01);
  max-width: 1350px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  height: 70px;
}

.links {
  margin-left: 200px;
  display: flex;
  align-items: center; /* Centra verticalmente los enlaces */
  flex: 1; /* Ocupa el espacio restante */
  justify-content: center; /* Centra los enlaces horizontalmente */
}

.links a {
  position: relative;
  margin: 0 20px; /* Espacio entre enlaces */
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  color: #070817;
  transition: color 0.3s;
}

.links a:hover {
  color: #51C764;
}


.logo {
  max-width: 100%;
  height: auto;
  max-height: 38px;
  background-color: white;
  cursor: pointer;
  margin-right: auto;
}

.logo-link {
  background-color: transparent;
}

.cta {
  background-color: transparent;
}


.header-buttons {
  display: flex;
  align-items: center;
  margin-left: auto;
}




/* Mobile styles */
.menu-icon {
  display: none;
  flex-direction: column;
  cursor: pointer;
  margin-left: 20px;
}

.hamburger {
  width: 20px;
  height: 2px;
  background-color: #333;
  margin: 3px;
  transition: 0.4s;
  border-radius: 40px;
}

.hamburger.open {
  background-color: #51C764;
}

@media (max-width: 1000px) {
  .navbar {
    flex-wrap: wrap;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 0px;
    position: fixed;
    width: 100%;
    border-bottom: 0.5px solid #ddd;
  }

  .links {
    display: none;
    flex-direction: column;
    width: 100%;
    background-color: #ffff;
    text-align: center;
    transition: max-height 0.5s ease-in-out, transform 0.5s ease-in-out;
    overflow: hidden;
    max-height: 0;
    transform: translateY(-100%);
    position: fixed;
    top: 70px; /* Adjust if needed based on navbar height */
    left: 0;
    right: 0;
    z-index: 999;
    margin-left: 0px;
    padding-top: 150px;
  }

  .links.open {
    display: flex;
    max-height: 100vh; /* 75% of the viewport height */
    transform: translateY(0);
  }

  .links a {
    margin: 10px 0;
    font-size: 18px;
  }

  .header-button {
    border-radius: 30px;
    padding: 10px;
    margin-top: 10px;
    height: 55px;
    width: 90px;
    border-radius: 90px;

    color: #5080ed;
    background-color: #507fed24;
    box-shadow: 0px 4px 43px rgba(0, 0, 0, 0.07);
    transition: background-color 0.3s;
  }

  .header-buttons {
    display: flex;
    flex-direction: column;
    margin-bottom: 300px;
    width: 100%;
  }

  .menu-icon {
    display: flex;
  }

  .cta {
    display: none;
  }

  .logo {
    max-width: 100%;
    height: auto;
    max-height: 27px;
    background-color: white;
    cursor: pointer;
    margin-right: auto;
  }
}


