.password-display {
  margin: 60px auto; /* Centrar horizontalmente */
  padding-top: 25px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 50px;
  background-color: #ffff;
  max-width: 600px;
  height: auto;
  border-radius: 15px;
  border: solid 0.5px #ddd;
  box-shadow: 0px 4px 43px rgba(0, 0, 0, 0.07);
  transition: transform 0.7s ease-in-out;
}

.password-display:hover {
  transform: scale(1.01);
}

.password-display h2 {
  font-size: 18px;
  font-weight: 600;
  text-align: left;
  margin-bottom: 30px;
}

.password-box {
